* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding-left: 0;
}

.countries-item{
  padding: 0.5rem;
  width: 100%;
  display: flex;
  align-items: center;
}

.countries-item:nth-child(odd) {
  background-color: rgba(0, 0, 0, 0.07);
}

.country-item-flag {
  height: 30px;
  width: auto;
}

.country-item-name {
  display: inline;
  margin-left: 2rem;
  font-size: 1rem;
  font-weight: 700;
  font-style: normal;
}

.country-name {
  display: inline;
}
